import Card from "./Card";
import React from "react";
import Sdata from "./Sdata";

const Services = () => {
    return(
        <>
            <div className="container">
                <div className="row">
                    <div className="col text-center user-select-none">
                        <h1 className="brand py-5">Services</h1>
                        <div className="row">
                            {
                                Sdata.map((val,index) => {
                                    return(
                                        <Card 
                                            key = {index}
                                            image = {val.imgsrc}
                                            title = {val.title}
                                            text = {val.text}
                                        />
                                    );
                                })
                            }
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default Services;