import React from "react";
import Common from "./Common";
import aboutimg from "./image/About.svg";

const txt = "Lorem ipsum dolor sit amet consectetur adipisicing elit. Laudantium, voluptatum. Asperiores voluptate obcaecati mollitia, tempore voluptas repellat accusantium deserunt enim."

const About = () => {
    return(
        <>
            <Common
                title = "About"
                text = {txt} 
                link = "/services"  
                homeimg = {aboutimg}
            />
        </>
    );
}

export default About;