import React from "react";
import { NavLink } from "react-router-dom";

const Navbar = () => {
    return(
        <>
            <div className="container">
                <div className="row">
                    <div className="col user-select-none">
                       <nav className="navbar navbar-expand-lg navbar-light">
                          <NavLink className="navbar-brand brand" to="/">SHIVANK</NavLink>
                          <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                            <span className="navbar-toggler-icon"></span>
                          </button>

                          <div className="collapse navbar-collapse" id="navbarSupportedContent">
                            <ul className="navbar-nav ml-auto">
                            <li className="nav-item">
                                <NavLink exact activeClassName="active-nav" className="nav-link" to="/">HOME</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink activeClassName="active-nav" className="nav-link" to="/about">ABOUT</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink activeClassName="active-nav" className="nav-link" to="/services">SERVICES</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink activeClassName="active-nav" className="nav-link" to="/contact">CONTACT</NavLink>
                            </li>
                            </ul>
                          </div>
                        </nav>
                    </div>
                </div>
            </div>
        </>
    );
}

export default Navbar;