import React, { useState } from "react";

const Contact = () => {

    const [data, setData] = useState(
        {
            fullname : "",
            email : "",
            subject : "",
            message : ""
        }
    );

    const InputEvent = (event) => {
        const {name,value} = event.target;

        setData((preVal) =>{
            return{
                   ...preVal,
                   [name] : value
            };
        })
    }

    const formSubmit = (e) => {
        e.preventDefault();
        alert(`Thank you for your message. We will response you asap ${data.fullname}`)

    }


    return(
        <>
            <div className="container">
                <div className="row">
                    <div className="col user-select-none">
                        <h1 className="headline text-center py-5">Contact Us</h1>
                        <div className="row">
                            <div className="col-md-6 mx-auto">
                            <form onSubmit={formSubmit}>
                                <div class="form-group">
                                    <label for="exampleInputName">Name</label>
                                    <input type="text" class="form-control" id="exampleInputName" name="fullname" value={data.name} onChange={InputEvent} required placeholder="Enter your name" />
                                </div>
                                <div class="form-group">
                                    <label for="exampleInputEmail">Email <address></address></label>
                                    <input type="email" class="form-control" id="exampleInputEmail" name="email" value={data.email} onChange={InputEvent} required placeholder="Enter your email" />
                                </div>
                                <div class="form-group">
                                    <label for="exampleInputSubject">Subject</label>
                                    <input type="text" class="form-control" id="exampleInputSubject" name="subject" value={data.subject} onChange={InputEvent} required placeholder="Enter your subject" />
                                </div>
                                <div class="form-group">
                                    <label for="exampleFormControlTextarea1">Message</label>
                                    <textarea class="form-control" id="exampleFormControlTextarea1" rows="3"  name="message" value={data.message} onChange={InputEvent} required placeholder="Enter your message..."></textarea>
                                </div>
                                <button type="submit" class="btn-get-started">Submit</button>
                            </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default Contact;