import React from "react";
import Common from "./Common";
import homeimg from "./image/Home.svg";

const txt = "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Possimus assumenda, error fugiat similique excepturi enim.";

const Home = () => {
    return(
        <>
            <Common
                title = "Home"
                text = {txt} 
                link = "/about"  
                homeimg = {homeimg}
            />
        </>
    );
}

export default Home;