import React from "react";
import { NavLink } from "react-router-dom";

const Common = (props) => {
    return(
        <>
            <div className="container">
                <div className="row">
                    <div className="col my-5">
                        <div className="row">
                            <div className="col-md-6 order-md-1 order-2 my-auto">
                                <h1 className="font-weight-bold">Welcome to <span className="page-title">{props.title}</span> page.</h1>
                                <h3 className="my-5">{props.text}</h3>
                                <NavLink to={props.link} className="btn-get-started user-select-none">Read More</NavLink>
                            </div>
                            <div className="col-md-6 order-md-2 order-1 user-select-none">
                                <img src={props.homeimg} className="img-fluid animated" alt="click"/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default Common;